import React from 'react';
import { BottomNavigation, BottomNavigationAction, Box } from '@mui/material';
import { Link } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import SendIcon from '@mui/icons-material/Send';


const AlaValikko : React.FC = () : React.ReactElement  => {

    return (
      <Box sx={{ width: 500, marginLeft:"30%", marginTop:"1em"}}>
        <BottomNavigation
        style={{backgroundColor:"rgba(172, 173, 203, 0.8)", borderRadius:"10px", boxShadow: '2px 2px 10px rgba(0, 0, 0, 0.2)'}}
        showLabels
        >

            <BottomNavigationAction label="Etusivu" component={Link} to="/" icon={<HomeIcon />} />
            <BottomNavigationAction label="Lisää" component={Link} to="/Lisays" icon={<AddCircleOutlineIcon />}/>
            <BottomNavigationAction label="Lähetys" component={Link} to="/Lahetys" icon={<SendIcon />} />
        </BottomNavigation>
      </Box>
    );
  }
  
  export default AlaValikko;