import { Button, Container, Typography } from '@mui/material';
import React from 'react';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';

interface Props {
    verenpaineArvot : VerenpaineArvot[];
    setVerenpaineArvot : (arg0 : VerenpaineArvot[]) => void
  }

const Poista : React.FC<Props> = ({verenpaineArvot, setVerenpaineArvot}) : React.ReactElement  => {

    const navigate : NavigateFunction = useNavigate();

    const {id} = useParams<any>();

    const poistaArvo : VerenpaineArvot | undefined = verenpaineArvot.find((verenpaineArvot : VerenpaineArvot) => {
        return verenpaineArvot.id === id;
    });

    const vahvistaPoisto = () : void => {
        setVerenpaineArvot([...verenpaineArvot.filter((verenpaineArvot : VerenpaineArvot ) => verenpaineArvot.id !== id)]);
        navigate("/");
    }

    return (
        <Container sx={{textAlign:"center"}}>
            <Typography variant='h3' sx={{marginBottom:".5em"}}> Poista verenpainearvo</Typography>
            <Typography variant='h5' sx={{marginBottom:"1em"}}> Haluatko varmasti poistaa {poistaArvo?.pvm?.toLocaleDateString()} verenpaine arvon: {poistaArvo!.ylapaine}/{poistaArvo!.alapaine} p. {poistaArvo!.pulssi}? </Typography>

            <Button
            variant='contained'
            onClick={vahvistaPoisto}
            sx={{marginRight:"2em"}}
            >Poista verenpainearvo</Button>

            <Button
            variant='contained'
            component={Link}
            to="/"
            >Peruuta</Button>
        </Container>
    );
  }
  
  export default Poista;