import { Container, List, ListItem, ListItemText, Typography } from '@mui/material';
import React from 'react';
import AlaValikko from './AlaValikko';
import { useLocation } from 'react-router-dom';
import { format } from 'date-fns';

interface Props {
  verenpaineArvot : VerenpaineArvot[];
  setVerenpaineArvot : (arg0 : VerenpaineArvot[]) => void
}

const Yhteenveto : React.FC<Props> = ({verenpaineArvot, setVerenpaineArvot}) : React.ReactElement  => {

  const location = useLocation();
  const formData = location.state?.formData;
  const jarjestettyArvot = [...verenpaineArvot].sort((a, b) => b.pvm.getTime() - a.pvm.getTime());

    return (
      <Container sx={{backgroundColor:"rgba(87, 203, 198, 0.22)", paddingBottom:"1em"}}>
        <Typography variant='h5' sx={{paddingTop:"1em"}}>Tiedot on lähetetty onnistuneesti ammattilaiselle. Sinuun ollaan yhteydessä {formData.yhteydenottotapa} viikon kuluessa.</Typography>
        <Typography variant='h5' sx={{marginTop:"1em", marginBottom:"1em", textAlign:"center"}}>Ilmoittamasi tiedot: </Typography>
        <Container sx={{width: 550, margin: "auto", border:"1px solid"}}>
        <Typography variant='h6'>Nimi: {formData.etunimi} {formData.sukunimi} </Typography>
        <Typography variant='h6'>Puhelinnumero: {formData.numero}</Typography>
        <Typography variant='h6'>Syntymäaika: {format(new Date(formData.syntymaAika), "dd.MM.yyyy")}</Typography>
        <Typography variant='h6'>Sähköpostiosoite: {formData.sahkoposti}</Typography>
        <Typography variant='h6'>Lääkitys: {formData?.laakitys || "Ei lääkitystä"}</Typography>
        <Typography variant='h6'>Valittu yhteydenottotapa: {formData.yhteydenottotapa}</Typography>
        </Container>
        <Typography variant='h5' sx={{marginTop:"1em", marginBottom:"1em", textAlign:"center"}}>Lähetetyt verenpainearvot: </Typography>

        <Container sx={{textAlign:"center", border:"1px solid", width: 550}}>
        <List sx={{margin:"auto"}}>
        {jarjestettyArvot.map((verenpaineArvot : VerenpaineArvot) => (
        <ListItem key={verenpaineArvot.id} sx={{textAlign: "center"}}>
                <ListItemText primary={format(verenpaineArvot.pvm, "dd.MM.Y HH:mm")}
                secondary={(
                  <React.Fragment>
                    <Typography variant='body1' color="textPrimary">{verenpaineArvot.ylapaine} / {verenpaineArvot.alapaine} p. {verenpaineArvot.pulssi} </Typography>
                    <Typography variant='body1' color="textPrimary">Verenpainearvoon vaikuttavat tekijät: {verenpaineArvot.muut}</Typography>
                  </React.Fragment>
                )}/>
              </ListItem>
        ))}
        </List>
        </Container>
      <AlaValikko/>
      </Container>
    );
  }
  
  export default Yhteenveto;