import { AppBar, Typography, Toolbar, CssBaseline, IconButton, Drawer, List, ListItemButton, ListItemText, ListItemIcon } from "@mui/material";
import React, { useState } from "react";
import MenuIcon from '@mui/icons-material/Menu'
import HomeIcon from '@mui/icons-material/Home';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import SendIcon from '@mui/icons-material/Send';
import { Link } from "react-router-dom";

const YlaValikko : React.FC = () : React.ReactElement => {

    const [valikkoAuki, setValikkoAuki] = useState<boolean>(false);

    return (
        <CssBaseline>
        <AppBar position="static" sx={{marginBottom:"1em"}}>
            <Toolbar style={{backgroundColor:"rgba(172, 173, 203, 0.8)"}}>
                <IconButton color="inherit"
                edge="start"
                onClick={() => setValikkoAuki(true)}>

                    <MenuIcon/>
                </IconButton>
                <Drawer open={valikkoAuki}
                onClose={() => setValikkoAuki(false)}>

                <List sx={{width : "350px", marginTop:"1em"}}  onClick={() => setValikkoAuki(false)}>
    
                    <ListItemButton
                    component={Link}
                    to="/">
                        <ListItemIcon>
                            <HomeIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Etusivu"/>
                    </ListItemButton>

                    <ListItemButton
                    component={Link}
                    to="/Lisays">
                        <ListItemIcon>
                            <AddCircleOutlineIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Lisää verenpainearvo"/>
                    </ListItemButton>

                    <ListItemButton
                    component={Link}
                    to="/Lahetys">
                        <ListItemIcon>
                            <SendIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Lähetä arvot ammattilaiselle"/>
                    </ListItemButton>
                </List>

            </Drawer>
            <Typography sx={{fontSize:"2em", marginLeft:"36%"}}>Verenpaineen seurantalomake</Typography>
            </Toolbar>

        </AppBar>
        </CssBaseline>
    );

}

export default YlaValikko;