import { Button, Container, FormControl, FormControlLabel, FormHelperText, FormLabel, Radio, RadioGroup, TextField, Typography } from '@mui/material';
import React, { useRef, useState } from 'react';
import AlaValikko from './AlaValikko';
import {SystemStyleObject} from '@mui/system'
import { useNavigate } from 'react-router-dom';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import {fi} from 'date-fns/locale/fi'
import {AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3'

const lomakeTyyli : SystemStyleObject = {
  marginBottom : "1em"
}

export interface Lomaketiedot {
  [key : string] : any;
  etunimi? : string;
  sukunimi? : string;
  numero? : number | string;
  sahkoposti? : string;
  syntymaAika? : Date | string;
  laakitys? : string;
  yhteydenottotapa? : boolean | string;
}

interface Errors extends Lomaketiedot{}

const ArvojenLahetys : React.FC = () : React.ReactElement  => {
  
  const navigate = useNavigate();

  const [virheIlmoitus, setVirheilmoitus] = useState<Errors>({});

  const lomaketiedot : Lomaketiedot = useRef<Lomaketiedot>({});

  const [syntymaPaiva, setSyntymapaiva] = useState<Date | null>(null);

  const lomakeKasittely = (e: React.FormEvent) : void => {
    e.preventDefault();
    console.log(lomaketiedot.current);
    let errors : Errors = {};

    if (!lomaketiedot.current.etunimi) {
        errors = {...errors, etunimi : "Etunimi puuttuu"};
    } else {
      if ((lomaketiedot.current.etunimi).length !<2){
        errors = {...errors, etunimi : "Etunimi on liian lyhyt."};
      }
    }

    if (!lomaketiedot.current.sukunimi) {
        errors = {...errors, sukunimi : "Sukunimi puuttuu"};
    } else {
      if ((lomaketiedot.current.sukunimi).length !<2 ){
        errors = {...errors, sukunimi : "Sukunimi on liian lyhyt."};
      }
    }

    if (!lomaketiedot.current.numero){
        errors = {...errors, numero : "Puhelinnumero puuttuu"};
    } else {
      if ((lomaketiedot.current.numero).length !<7){
        errors = {...errors, numero : "Puhelinnumero on liian lyhyt"};
      }
    }

    if (!lomaketiedot.current.sahkoposti){
      errors = {...errors, sahkoposti : "Sähköpostiosoite puuttuu"};
    } else {
      if (lomaketiedot.current.sahkoposti.search("@") === -1) {
        errors = {...errors, sahkoposti : "Virheellinen sähköpostiosoite"};
      }
    }

    if (!lomaketiedot.current.syntymaAika ){
    errors = {...errors, syntymaAika : "Syntymäaika puuttuu"};
    }

    if (!lomaketiedot.current.yhteydenottotapa) {
        errors = {...errors, yhteydenottotapa : "Valitse yhteydenottotapa"};
    }

    if (Object.entries(errors).length > 0) {
        setVirheilmoitus({...errors});
    } else {
        setVirheilmoitus({});
        navigate("/Yhteenveto", {state: {formData: lomaketiedot.current}});
    }
};

const syoteKasittely = (e : React.ChangeEvent<HTMLInputElement>) : void => {
    lomaketiedot.current[e.target.name] = (e.target.type === "checkbox") ? Boolean(e.target.checked) : e.target.value;
};

const kasittelepaiva = (date: Date | null) => {
  setSyntymapaiva(date);
  lomaketiedot.current['syntymaAika'] = date;
};

    return (
      <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
      <Container>
        <Typography variant='h4' sx={lomakeTyyli}>Täytä lomake lähettääksesi tiedot terveydenhuollon ammattilaiselle</Typography>
        <form onSubmit={lomakeKasittely}>
          <TextField
          variant='filled'
          fullWidth={true}
          sx={lomakeTyyli}
          name='etunimi'
          label="Etunimi"
          placeholder='Etunimi'
          onChange={syoteKasittely}
          error={Boolean(virheIlmoitus.etunimi)}
          helperText={virheIlmoitus.etunimi}/>

        <TextField
          variant='filled'
          fullWidth={true}
          sx={lomakeTyyli}
          name='sukunimi'
          label="Sukunimi"
          placeholder='Sukunimi'
          onChange={syoteKasittely}
          error={Boolean(virheIlmoitus.sukunimi)}
          helperText={virheIlmoitus.sukunimi}/>

          <DatePicker
          label="Syntymäaika"
          value={syntymaPaiva}
          sx={{marginBottom:"1em"}}
          onChange={kasittelepaiva}
          slots={{
            textField: (params) => (
                 <TextField
                 variant='filled'
                  {...params}
                   fullWidth={true}
                     error={!!virheIlmoitus.syntymaAika}
                     helperText={virheIlmoitus.syntymaAika || ""} />
                  )
                  }}
          />

          <TextField
          variant='filled'
          fullWidth={true}
          sx={lomakeTyyli}
          name='numero'
          label="Puhelinnumero"
          placeholder='Puhelinnumero'
          onChange={syoteKasittely}
          error={Boolean(virheIlmoitus.numero)}
          helperText={virheIlmoitus.numero}/>

          <TextField
          variant='filled'
          fullWidth={true}
          sx={lomakeTyyli}
          name='sahkoposti'
          label="Sähköpostiosoite"
          placeholder='Sähköpostiosoite'
          onChange={syoteKasittely}
          error={Boolean(virheIlmoitus.sahkoposti)}
          helperText={virheIlmoitus.sahkoposti}/>

          <TextField
          variant='filled'
          fullWidth={true}
          sx={lomakeTyyli}
          name='laakitys'
          label="Lääkitys"
          placeholder='Käytössä oleva verenpaine lääkitys'
          onChange={syoteKasittely}
          />

          <FormControl fullWidth style={{marginTop:"1em"}}>
                <FormLabel required>Valitse yhteydenottotapa</FormLabel>
                <RadioGroup >
                    <FormControlLabel
                    value="sähköisen asiointipalvelun kautta"
                    label="Toivon yhteydenottoa sähköisen asiointipalvelun kautta"
                    control={<Radio
                            name="yhteydenottotapa"
                            size="small"
                            onChange={syoteKasittely}
                            />
                    } />

                    <FormControlLabel
                    value="tekstiviestillä"
                    label="Toivon yhteydenottoa tekstiviestillä"
                    control={<Radio
                            name="yhteydenottotapa"
                            size="small"
                            onChange={syoteKasittely}
                            />
                    } />

                    <FormControlLabel
                    value="puhelimitse"
                    label="Toivon yhteydenottoa puhelimitse"
                    control={<Radio
                            name="yhteydenottotapa"
                            size="small"
                            onChange={syoteKasittely}
                            />
                    } />


                </RadioGroup>
                <FormHelperText error={Boolean(virheIlmoitus.yhteydenottotapa)}>{virheIlmoitus.yhteydenottotapa}</FormHelperText>
            </FormControl>

            <Button 
            type="submit"
            color="inherit"
            fullWidth={true}
            variant="contained"
            sx={{margin:"2em"}}
            >Lähetä tiedot</Button>
      
        </form>
      <AlaValikko/>
      </Container>
      </LocalizationProvider>
    );
  }
  
  export default ArvojenLahetys;