import React, { useEffect, useRef, useState } from 'react';
import { Button, Container, TextField, Typography } from '@mui/material';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { DateTimePicker, LocalizationProvider } from '@mui/x-date-pickers';
import {fi} from 'date-fns/locale/fi'
import {AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3'

interface Props {
  verenpaineArvot : VerenpaineArvot[];
  setVerenpaineArvot : (arg0 : VerenpaineArvot[]) => void
}

interface Errors {
  ylapaine : string,
  alapaine : string,
  pulssi : string,
  pvm : string,
}

const Muokkaa : React.FC<Props> = ({verenpaineArvot, setVerenpaineArvot}) : React.ReactElement  => {

  const [pvm, setPvm] = useState<Date>(new Date());
  const {id} = useParams<any>();
  const navigate: NavigateFunction = useNavigate();

  const ylapaineRef = useRef<HTMLInputElement | undefined>();
  const alapaineRef = useRef<HTMLInputElement | undefined>();
  const pulssiRef = useRef<HTMLInputElement | undefined>();
  const muutRef = useRef<HTMLInputElement | undefined>();

  const [virheIlmoitus, setVirheilmoitus] = useState<Errors>({
    ylapaine : "",
    alapaine : "",
    pulssi : "",
    pvm : ""
  });

  useEffect(() => {
    const valittuArvo: VerenpaineArvot | undefined = verenpaineArvot.find((verenpaineArvot : VerenpaineArvot) => verenpaineArvot.id === id);
    if (valittuArvo) {
      setPvm(valittuArvo.pvm);
    }
  }, [verenpaineArvot, id]);

  const muokkaaArvo = () => {
    
    setVirheilmoitus({
      ylapaine: "",
      alapaine: "",
      pulssi: "",
      pvm: ""
    });

    let virhe = false;

    if (!ylapaineRef.current!.value || !/^\d+$/.test(ylapaineRef.current!.value)){
      setVirheilmoitus(prevState => ({
        ...prevState,
        ylapaine: "Arvon tulee sisältää vain numeroita tai arvo puuttuu"
      }));
      virhe = true;
    }

    if (!alapaineRef.current!.value || !/^\d+$/.test(alapaineRef.current!.value)){
      setVirheilmoitus(prevState => ({
        ...prevState,
        alapaine: "Arvon tulee sisältää vain numeroita tai arvo puuttuu"
      }));
      virhe = true;
    }

    if (!pulssiRef.current!.value || !/^\d+$/.test(pulssiRef.current!.value)){
      setVirheilmoitus(prevState => ({
        ...prevState,
        pulssi: "Arvon tulee sisältää vain numeroita tai arvo puuttuu"
      }));
      virhe = true;
    }

    if (pvm >= new Date()) {
      setVirheilmoitus(prevState => ({
          ...prevState,
          pvm: "Aloitusaika on oltava menneisyydessä"
      }));
      virhe = true;
  }

  if (virhe) {
    return;
  }
    const muokatutArvot = verenpaineArvot.map((verenpaineArvot : VerenpaineArvot) => {
      if (verenpaineArvot.id === id) {
        return {
          ...verenpaineArvot,
          ylapaine : ylapaineRef.current!.value || "Yläpaine puuttuu",
          alapaine : alapaineRef.current!.value || "Alapaine puuttuu",
          pulssi : pulssiRef.current!.value || "Pulssi puuttuu",
          pvm : pvm,
          muut : muutRef.current!.value || "-"
        };
      }
      return verenpaineArvot;
    });
    setVerenpaineArvot(muokatutArvot);
    navigate("/");
  }
  const valittuArvo: VerenpaineArvot | undefined = verenpaineArvot.find((verenpaineArvot : VerenpaineArvot) => verenpaineArvot.id === id);

    return (
      <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
        <Container sx={{textAlign:"center"}}>
          <Typography variant='h3'
          sx={{marginBottom:"1em"}}
          >Muokkaa verenpainearvoa "{valittuArvo?.pvm?.toLocaleDateString()} {valittuArvo?.ylapaine}/{valittuArvo?.alapaine} P. {valittuArvo?.pulssi}" </Typography>

          <DateTimePicker 
        label="Mittauksen päivänmäärä ja kellonaika"
        value={pvm}
        maxDate={new Date()}
        onChange={(uusipvm: Date | null) => setPvm(uusipvm || new Date())}
        slots={{
          textField: (params) => (
            <TextField 
            {...params}
            sx={{marginBottom:"1em"}}
            error={!!virheIlmoitus.pvm}
            helperText={virheIlmoitus.pvm || ""}/>
          )
        }} /> 
        <br></br>

        <TextField label="Yläpaine"
        inputRef={ylapaineRef}
        variant='outlined'
        defaultValue={valittuArvo?.ylapaine}
        error={!!virheIlmoitus.ylapaine}
        helperText={virheIlmoitus.ylapaine ? virheIlmoitus.ylapaine : "Systolinen paine"}
        sx={{marginBottom:"1em", marginRight:"1em"}}/>

      <TextField label="Alapaine"
        inputRef={alapaineRef}
        variant='outlined'
        defaultValue={valittuArvo?.alapaine}
        error={!!virheIlmoitus.alapaine}
        helperText={virheIlmoitus.alapaine ? virheIlmoitus.alapaine : "Diastolinen paine"}
        sx={{marginBottom:"1em", marginRight:"1em"}}/> 

      <TextField label="Pulssi"
        inputRef={pulssiRef}
        variant='outlined'
        defaultValue={valittuArvo?.pulssi}
        error={!!virheIlmoitus.pulssi}
        helperText={virheIlmoitus.pulssi}
        sx={{marginBottom:"1em"}}/>
        <br></br>

      <TextField label="Mittaukseen vaikuttavat tekijät: "
        inputRef={muutRef}
        variant='outlined'
        defaultValue={valittuArvo?.muut}
        helperText="Esimerkiksi raskas liikunta tai kahvi 30 min ennen mittausta"
        sx={{marginBottom:"1em"}}/>
        <br></br>

        <Button
        variant='contained'
        color='inherit'
        onClick={muokkaaArvo}
        sx={{marginRight:"1em"}}>Tallenna</Button>
        
        <Button variant='contained'
        color='inherit'
        component={Link}
        to="/"
        >Peruuta</Button>
      </Container>
      </LocalizationProvider>
    );
  }
  
  export default Muokkaa;