import { Container, FormControl, IconButton, InputLabel, List, ListItem, ListItemIcon, ListItemText, MenuItem, Select, SelectChangeEvent, Typography } from '@mui/material';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import ModeIcon from '@mui/icons-material/Mode';
import DeleteIcon from '@mui/icons-material/Delete';
import { format, startOfWeek, subDays } from 'date-fns';
import AlaValikko from './AlaValikko';

interface Props {
  verenpaineArvot : VerenpaineArvot[];
  setVerenpaineArvot : (arg0 : VerenpaineArvot[]) => void
  verenpaineKeskiarvot : any
}

const Verenpainelomake : React.FC<Props> = ({verenpaineArvot, setVerenpaineArvot, verenpaineKeskiarvot}) : React.ReactElement  => {

      const [valittuKeskiarvo, setValittuKeskiarvo] = useState<string>("");
      const [verenpaineKeskiarvo, setVerenpaineKeskiarvo] = useState<string>("");
      const [verenpaineSelite, setVerenpaineSelite] = useState<string>("")

      const laskeKeskiarvo = (aloitusAika: Date, lopetusAika: Date) => {
        const jarjestettyData = verenpaineArvot.filter((arvo) => arvo.pvm >= aloitusAika && arvo.pvm <= lopetusAika);

        const ylapaineSumma =  jarjestettyData.reduce((sum, arvo) => sum + parseFloat(arvo.ylapaine), 0);
        const alapaineSumma =  jarjestettyData.reduce((sum, arvo) => sum + parseFloat(arvo.alapaine), 0);
        const pulssiSumma =  jarjestettyData.reduce((sum, arvo) => sum + parseFloat(arvo.pulssi), 0);

        const ylapainekeskiarvo = jarjestettyData.length > 0 ? ylapaineSumma / jarjestettyData.length : 0;
        const alapainekeskiarvo = jarjestettyData.length > 0 ? alapaineSumma / jarjestettyData.length : 0;
        const pulssikeskiarvo = jarjestettyData.length > 0 ? pulssiSumma / jarjestettyData.length : 0;

        if (ylapainekeskiarvo < 120) {
          setVerenpaineSelite("Verenpaineesi on ihanteellinen");
      } if (ylapainekeskiarvo >= 120 && ylapainekeskiarvo <= 129) {
          setVerenpaineSelite("Verenpaineesi on normaali");
      } if (ylapainekeskiarvo >= 130 && ylapainekeskiarvo <= 139) {
          setVerenpaineSelite ("Verenpaineesi on lievästi koholla");
      } if (ylapainekeskiarvo >= 140) {
          setVerenpaineSelite("Verenpaineesi on koholla");
      } 

        return {
          ylapainekeskiarvo,
          alapainekeskiarvo,
          pulssikeskiarvo
        };
      };

      const handleSelectChange = (e: SelectChangeEvent) => {
        setValittuKeskiarvo(e.target.value);
        const nyt = new Date();
        let startDate, endDate;
    
        if (e.target.value === "Viikko") {
          startDate = startOfWeek(nyt);
          endDate = nyt;
        } else if (e.target.value === "Neljä vuorokautta") {
          startDate = subDays(nyt, 3); 
          endDate = nyt;
        } else {
          startDate = null;
          endDate = null;
        }
    
        if (startDate && endDate) {
          const average = laskeKeskiarvo(startDate, endDate);
          setVerenpaineKeskiarvo(`Keskiarvo ajalta ${format(startDate, "dd.MM.y")} - ${format(endDate, "dd.MM.y")}: ${Math.round(average.ylapainekeskiarvo)}/${Math.round(average.alapainekeskiarvo)} p. ${Math.round(average.pulssikeskiarvo)}`);
        } else {
          const average = laskeKeskiarvo(new Date(0), nyt);
          setVerenpaineKeskiarvo(`Keskiarvo kaikista arvoista: ${Math.round(average.ylapainekeskiarvo)}/${Math.round(average.alapainekeskiarvo)} p. ${Math.round(average.pulssikeskiarvo)}`);
        }
      };
      
    
      const jarjestettyArvot = [...verenpaineArvot].sort((a, b) => b.pvm.getTime() - a.pvm.getTime());


    return (
      <Container sx={{textAlign:'center', backgroundColor:"rgba(87, 203, 198, 0.22)", padding:"2em", boxShadow: '2px 2px 10px rgba(0, 0, 0, 0.2)'}}>
        <Typography variant='h3' sx={{marginBottom:".5em", marginTop:".2em"}}>Lisätyt verenpainearvot</Typography>

        <List sx={{ maxWidth: "650px", margin:"auto"}}>

            {jarjestettyArvot.map((verenpaineArvot : VerenpaineArvot) => (
              <ListItem key={verenpaineArvot.id} sx={{textAlign: "center"}} style={{borderBottom:"1px solid", boxShadow: '2px 2px 10px rgba(0, 0, 0, 0.2)'}}>
                <ListItemIcon>
                  <IconButton
                  component={Link}
                  to={`/Muokkaa/${verenpaineArvot.id}`}>
                    <ModeIcon />
                  </IconButton>
                </ListItemIcon>
                <ListItemText primary={format(verenpaineArvot.pvm, "dd.MM.y HH:mm")}
                secondary={(
                  <React.Fragment>
                    <Typography variant='body1' color="textPrimary">{verenpaineArvot.ylapaine} / {verenpaineArvot.alapaine} p. {verenpaineArvot.pulssi} </Typography>
                    <Typography variant='body1' color="textPrimary">Verenpainearvoon vaikuttavat tekijät: {verenpaineArvot.muut}</Typography>
                  </React.Fragment>
                )}/>
                <ListItemIcon>
                  <IconButton
                  component={Link}
                  to={`/Poista/${verenpaineArvot.id}`}>
                  <DeleteIcon/>
                  </IconButton>
                
                </ListItemIcon>
              </ListItem>
            ))}
        <FormControl sx={{ marginTop: "1.5em", width: 400}}>
        <InputLabel id="verenpaineKeskiarvoLabel">Laske verenpaineiden keskiarvo ajalta:</InputLabel>
        <Select
         labelId="verenpaineKeskiarvoLabel"
         label="Laske verenpaineiden keskiarvo ajalta:"
         value={valittuKeskiarvo}
         onChange={handleSelectChange}
         >
          {verenpaineKeskiarvot.map((keskiarvo: string, idx: number) => {
           return <MenuItem key={idx} value={keskiarvo}>{keskiarvo}</MenuItem>
          })}
          </Select>
        </FormControl>


          <Typography variant='h5' sx={{marginTop:"1em"}}>{verenpaineKeskiarvo}</Typography>
          <Typography variant='h5' sx={{marginTop:"1em"}}>{verenpaineSelite}</Typography>
        </List>

        <AlaValikko/>
      </Container>
    );
  }
  
  export default Verenpainelomake;