import React, { useEffect, useRef, useState } from 'react';
import {Route, Routes } from 'react-router-dom';
import Verenpainelomake from './components/Verenpainelomake';
import ArvonLisays from './components/ArvonLisays';
import ArvojenLahetys from './components/ArvojenLahetys';
import Yhteenveto from './components/Yhteenveto';
import YlaValikko from './components/YlaValikko';
import Muokkaa from './components/Muokkaa';
import Poista from './components/Poista';
import data from './data.json';

const App : React.FC = () : React.ReactElement  => {

  console.log(data)

  const hakuAloitettu : React.MutableRefObject<boolean> = useRef(false);

const [verenpaineArvot, setVerenpaineArvot] = useState<VerenpaineArvot[]>([]);

useEffect(() => {
  if (!hakuAloitettu.current) {
    if (localStorage.getItem("arvot")) {
      setVerenpaineArvot(JSON.parse(String(localStorage.getItem("arvot"))).map((verenpaineArvot : VerenpaineArvot) => {
        return {
          ...verenpaineArvot, 
          pvm : new Date(verenpaineArvot.pvm)
        }
      }))
    }
  }
  return () => {
    hakuAloitettu.current = true;
  }
}, []);

useEffect(() => {
  localStorage.setItem("arvot", JSON.stringify(verenpaineArvot));
}, [verenpaineArvot]);

  return (
    <>
    <YlaValikko/>
      <Routes>
      <Route path='/' element={<Verenpainelomake verenpaineKeskiarvot={data.verenpaineKeskiarvot} verenpaineArvot={verenpaineArvot} setVerenpaineArvot={setVerenpaineArvot}/>}/>
      <Route path='/Lisays' element={<ArvonLisays  verenpaineArvot={verenpaineArvot} setVerenpaineArvot={setVerenpaineArvot}/>}/>
      <Route path='/Lahetys' element={<ArvojenLahetys/>}/>
      <Route path='/Yhteenveto' element={<Yhteenveto verenpaineArvot={verenpaineArvot} setVerenpaineArvot={setVerenpaineArvot}/>}/>
      <Route path='/Muokkaa/:id' element={<Muokkaa verenpaineArvot={verenpaineArvot} setVerenpaineArvot={setVerenpaineArvot}/>}/>
      <Route path='/Poista/:id' element={<Poista verenpaineArvot={verenpaineArvot} setVerenpaineArvot={setVerenpaineArvot}/>}/>
      </Routes>
    </>
  );
}

export default App;
